import React, { useEffect, useState } from 'react'
import Header from "../components/Header/Header"
import "../styles/services.css"
import Usericon from '../assets/images/user-icon.png'
import ReactStars from 'react-stars'
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import axios from 'axios'
import Cookiesservices from '../Services/Cookiesservices'
import { Link, Navigate, useNavigate } from 'react-router-dom'
import ChatHome from '../components/chat-home/ChatHome'
import LinksHome from '../components/links-home/LinksHome'
import User_icon from "../assets/images/user-icon.png"
import { toast } from 'react-toastify'

const Services = () => { 
    const[tab , settab]=useState("الخدمات")
    const token=Cookiesservices.get('jwt');
    const [data , setdata]=useState([])
    const [subscriptions , setsubscriptions]=useState([])
    const id_category=localStorage.getItem("id_category")
    const [Number,setNumber]=useState()
  
    useEffect(()=>{
      const fetchservices=async()=>{
        const response=await axios.get(`https://creen-program.com/api/services`,{
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        setdata(response.data.data)
      }
      const fetchsubscriptions=async()=>{
        const response=await axios.get(`https://creen-program.com/api/services/subscriptions`,{
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        setsubscriptions(response.data.data)
      
      }
      fetchservices();
      fetchsubscriptions()
    },[token])
    console.log(token)
    const [category_id, setcategoryid] = useState("")
    const [categories, setcatigories] = useState([])
    const [gender,setgender]=useState("")
    const [name,setname]=useState("")
    const [nationality,setnationality]=useState("")
    const [city,setcity]=useState("")
    const [education,seteducation]=useState("")
    const [nationalid,setnationalid]=useState("")
    const [phone,setphone]=useState("")
    const [email,setemail]=useState("")
    const [files,setFiles]=useState()
    const [description,setdescription]=useState([])
    const [joptitle,setjoptitle]=useState([])
    const[showservices , setshowservices]=useState(false)
   
    useEffect(()=>{
      const handlefetchcategortid = async () => {
        const respose = await axios.get(`https://creen-program.com/api/categories`, {
          headers: {
            'Content-type': 'application/json; charset=UTF-8',
            'accept': 'application/json'
            , Authorization: `Bearer ${token}`
          },
        })
        setcatigories(respose.data.data)
    
    
      }
      handlefetchcategortid()
     },[token])

    const handlechangecategoryid = (e) => {
      if (e.target.value) {
        setcategoryid(e.target.value)
  
      } else {
        // setcategoryid("")
        // setcatigories()
      }
    }
    const handleFileChange = (e) => {
      setFiles(e.target.value);
    };
    const handleUpload = async () => {
      try {
        const apiUrl = "https://creen-program.com/api/services/add";
        
  
        const formData = new FormData();
  
        // files.forEach((file, index) => {
        //   formData.append(`photos[${index}]`, file);
        // });
        formData.append("category_id", category_id);
         formData.append("photos", files);
        formData.append("name", name);
        formData.append("phone", phone);
        formData.append("email", email);
        formData.append("nationalid", nationalid);
        formData.append("natoinality", nationality);
        formData.append("education", education);
        formData.append("city", city);
        formData.append("gender", gender);
        formData.append("description", description);
        formData.append("joptitle", joptitle);






        
  
  
        const response = await axios.post(apiUrl,formData, {
          headers: {
            Authorization: `Bearer ${token}`,
          
          },
          body: formData,
        });
  
         if (response.status===200) {
           toast.success("File uploaded successfully");
           console.log(response)
           setshowservices(!showservices)
         }
      } catch (error) {
        console.error('Error creating post:', error);
        if (error.response && error.response.status === 400) {
          console.error('Server response data:', error.response.data);
        }
        toast.error('An error occurred while creating the post');
      }
    

      }
  useEffect(()=>{
    const service=async()=>{
            const response=await axios.get(`https://creen-program.com/api/services/show/${id_category}`,{
                    headers:{
                            Authorization:`Bearer ${token}`
                    }
            })
            setNumber(response.data.data.trainers)
    }
    service()
},[id_category, token])
// const idd=Number[0].id;
localStorage.setItem("Number_id",21)
  const[num,setnum]=useState([])
  useEffect(()=>{
    const numreservations=async()=>{
      const response=await axios.get(`https://creen-program.com/api/services/showusers2/21`,{headers: {
    Authorization: `Bearer ${token}`,
  
  },
})
setnum(response.data.data.users)
    }
    numreservations()
  },[ token])
const navigate=useNavigate()  
  return (
    
    <>
    <Header/>
    < section style={{ width: "100%",height:`${showservices?"100%":"100vh"}`,display: 'flex', justifyContent: "space-between" }}>
        <ChatHome />
        <section className='center' style={{ width: "59%", display: "flex", justifyContent: "space-between", gap: "19px" }}>

        <div className="services" style={{    position:"relative",background: "#fff",padding:"10px 0px",height:"100%",width:"100%",paddingBottom:"30px"}}>
        <div className="navigation" style={{display:"flex",justifyContent:"space-around",background: "#fff",
    paddingTop: "10px",
    marginTop: "-9px"}}>
            <span className={ `${tab==='خدماتي'?'services_active': ''} `} onClick={()=>settab("خدماتي")}>خدماتي</span>
            <span className={ `${tab==='الاشتراكات'?'services_active': ''} `} onClick={()=>settab("الاشتراكات")}>الاشتراكات</span>
            <span className={ `${tab==='الخدمات'?'services_active': ''} `} onClick={()=>settab("الخدمات")}>الخدمات</span>
        </div>

        <div className="services-content" style={{marginTop:"10px"}}>
          {
            tab === "الخدمات" ? 
            <div className="services-services" style={{display:"grid",gridTemplateColumns:"repeat(3,1fr)",rowGap:"45px"}} >
              {
                data.map((item,index)=>(
                  <Link to={`/services/${item.id}`} key={index} style={{textDecoration:"none"}}>
                    <div  className="service-service" style={{display:"flex",flexDirection:"column",alignItems:"center",position:"relative"}}>
                <img src={item.icon} alt="" style={{    width: "95px",
    height: "95px",
    borderRadius: "50%",
    }}/>
                <span style={{    padding: "5px 15px",
    background: "#ddd",
    color: "#585555",
    borderRadius: "5px",
    position: "absolute",
    bottom: "-25px",
    fontSize:"12px"}}>{item.name}</span>
              </div>
                  </Link>

                ))

              }
              
              
            </div>
            :tab==="الاشتراكات" ?<>
            {
              subscriptions.usubscriptions.length===0 && subscriptions.esubscriptions.length===0?<p style={{    color: "#000",
                fontSize: "24px",
                height: "64vh",
                textAlign: "center"}}> لا يوجد اشتراكات</p>:<div className="Contributions" style={{display:"flex",flexWrap:"wrap",gap:"5px",justifyContent:"center"}}>
              <div className="Expiredsubscriptions" style={{display: "flex",
    flexWrap: "wrap",
    gap: "5px"}}>
              {
                subscriptions.esubscriptions.map((item,index)=>(
                  <div className="Expiredsubscription" style={{    width: "290px",
    textAlign: "center",
    border: "1px solid #ddd",
    padding: "7px",
    borderRadius: "5px",
    margin: "auto",height:"160px",background:"#fff"}}>
              <button style={{border: "1px solid #80808014",
    background: "rgb(214 206 206 / 20%)",
    width: "65%",
    borderRadius: "16px",
    marginBottom: "5px",
    color: "red"}} >اشتراك منتهي</button >
    <div className="info-user-subs" style={{    display: "flex",
    justifyContent: "flex-end",gap:"11px"}}>
      <div className="information" style={{    display: "flex",
    flexDirection: "column"}}>
        <span style={{    fontSize: "14px",
    fontWeight: "bold",
        color: "rgb(102 99 99)"}}> كريم بدوي</span>
        <span style={{    fontSize: "14px",
    fontWeight: "bold",
        color: "rgb(102 99 99)"}}>مدرب لياقة</span>
        <ReactStars
  count={5} 
  value={5}
  size={17}
  color={'#ffd700'} />
      </div>
      <img src={Usericon} alt="" style={{width:"50px",height:"50px",borderRadius:"50%"}} />
    </div>
    <span style={{fontSize: "14px",
    fontWeight: "bold",
        color: "rgb(102 99 99)"}}>هل ترغب تجديد الإشتراك</span>
    <div className="buttons-subscription" style={{    display: "flex",
    justifyContent: "space-around",
    gap: "5px"}}>
      <button style={    {border: "none",
    width: "100%",
        background: "#c60a0a",
    borderRadius: "7px",
    color: "#fff"}}>No</button>
      <button style={    {border: "none",
    width: "100%",
        background: "rgb(4 118 4)",
    borderRadius: "7px",
    color: "#fff"}}>Ok</button>
    </div>

              </div>
                ))
              }
              
              </div>
              <div className="Validsubscriptions" style={{display:"flex",flexWrap:"wrap",gap:"5px"}}>
              {
                subscriptions.usubscriptions.map((item,index)=>(
                  <div className="Validsubscription" style={{    width: "290px",
    textAlign: "center",
    border: "1px solid #ddd",
    padding: "7px",
    borderRadius: "5px",
    margin: "auto",height:"160px",background:"#fff"}}>
              <button style={{border: "1px solid green",
    background: "#7d7d7d24",
    width: "65%",
    borderRadius: "16px",
    marginBottom: "5px",
    color: "#0ede0e"}} >اشتراك ساري</button >
    <div className="info-user-subs" style={{    display: "flex",
    justifyContent: "flex-end",gap:"11px"}}>
      <div className="information" style={{    display: "flex",
    flexDirection: "column"}}>
        <span style={{    fontSize: "14px",
    fontWeight: "bold",
        color: "rgb(102 99 99)"}}> كريم بدوي</span>
        <span style={{    fontSize: "14px",
    fontWeight: "bold",
        color: "rgb(102 99 99)"}}>مدرب لياقة</span>
        <ReactStars
  count={5} 
  value={5}
  size={17}
  color={'#ffd700'} />
      </div>
      <img src={Usericon} alt="" style={{width:"50px",height:"50px",borderRadius:"50%"}} />
    </div>
    <div className="data" style={{display: "flex",
    flexDirection: "column",
    justifyContent: "flex-end",
    alignItems: "end",
    gap: "6px"}}>
      <span className='Dateofsubscription' style={{    display: "flex",
    gap: "6px",
    color: "#ddd",
    fontWeight: "bold",
    fontSize: "14px"}}><span >12.01.2024</span><span>تاريخ الإشتراك</span></span>
      <span className='ExpiryDate'  style={{    display: "flex",
    gap: "6px",
    color: "#ddd",
    fontWeight: "bold",
    fontSize: "14px"}} ><span>12.02.2024</span><span>ينتهي بتاريخ</span></span>
    </div>
              </div>
                ))
              }
              
              </div>
             
             
              
              
            </div>
            }
            </>:tab==="خدماتي"?<>
            <div style={{    display: "flex",
    flexDirection: "column",
    alignItems: "end",
    gap: "5px",borderBottom:"1px solid #ddd"}}>
              <div className="heaa" style={{    display: "flex",
    justifyContent: "space-between",
    width: "96%"}}>
                <button style={{background: "transparent",
    border: "none",
    color: "green"}}>تعديل</button>
                <span>doctor</span>
                </div>
                <span>الصحة العامة</span>
                <span style={{display:"flex"}}><span>منذ 3 أسابيع</span>:<span>تاريخ التسجيل</span></span>
                <span><span><button style={{marginRight: "5px",
    border: "1px solid green",
    background: "transparent",
    padding: "6px",
    borderRadius: "25px"}} onClick={()=>navigate("/Participants")}>شاهد</button></span>{num.length}<span></span>:<span>عدد الحجوزات</span></span>
                <div style={{width:"100%",color:"green"}}><span>مفعل</span></div>
            </div>
            {
              showservices&&<div style={{display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "center",
              gap: "10px"}}>
                      <select required name="" id="" value={category_id} style={{ width: "90%" }} onChange={handlechangecategoryid}>
                                <option value="أختر"> أختر الاقسام</option>
                                {
                                  categories.map((item) => (
          
                                    <option value={item.id}>{item.name}</option>
                                  ))
                                }
                              </select>
                              <div className="gender form-check" style={{    display: "flex",
              marginTop: "15px",
              justifyContent: "center",
              gap: "16px"}}>
                                <div className="male" style={{display: "flex",
              alignItems: "center",
              gap: "5px"}}><label htmlFor="">ذكر</label>
                                <input type="radio" name='gender'  onClick={()=>{setgender("male")}} />
                                </div>
                                <div className="male" style={{display: "flex",
              alignItems: "center",
              gap: "5px"}}><label htmlFor="">انثي</label>
                                <input type="radio" name='gender'  onClick={()=>{setgender("female")}} />
                                </div>
                              </div>
                              <input type="text" placeholder='الاسم' onChange={(e)=>{setname(e.target.value)}}/>
                              <input type="text" placeholder='الجنسية' onChange={(e)=>{setnationality(e.target.value)}} />
          
                              <input type="text" placeholder='الدولة' onChange={(e)=>{setcity(e.target.value)}} />
                              <input type="text" placeholder='المؤهل التعليمي' onChange={(e)=>{seteducation(e.target.value)}} />
                              <input type="text" placeholder='المسمي الوظيفي' onChange={(e)=>{setjoptitle(e.target.value)}} />
                              <input type="text" placeholder='رقم الهوية' onChange={(e)=>{setnationalid(e.target.value)}} />
                              <input type="text" placeholder='رقم الجوال' onChange={(e)=>{setphone(e.target.value)}} />
                              <input type="text" placeholder='الأيميل' onChange={(e)=>{setemail(e.target.value)}} />
         
          <input type="text" placeholder='صور الشهادات والوثائق' onChange={handleFileChange} />
             
            <input type="text" placeholder='وصف الخدمة' onChange={(e)=>{setdescription(e.target.value)}} />
            <button style={{    border: "none",
              padding: "10px",
              width: "50%",
              background: "#0ce80c",
              color: "white",
              borderRadius: "9px"}} onClick={()=>{handleUpload()}}>تقديم الخدمة</button>
                          
                      </div>
          
            }            <span style={{    position: "absolute",
    bottom: "132px",
    left: "25px",
    zIndex: "2000000",
    background: "#000",
    width: "50px",
    height: "50px",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: "50%",
    color: "#fff"}} onClick={()=>{setshowservices(!showservices)}}><i class="ri-add-line"></i></span>

            
  
</>:<></>
          }
        </div>
    </div>
          

        </section>
        <LinksHome />

      </section>
      <div class="footer-mobile" style={{
        background: "#fff",
        border: "none",
        height: "48px",
        position: "sticky",
        zIndex: "9999",
        bottom: "0", display: "none"
      }}>
        <div class="container">
          <ul style={{
            paddingLeft: "0 !important", display: "flex",
            alignItems: "center",
            justifyContent: "space-between"
          }}>
            <li>

              <img src={User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" }} />

            </li>
            <li>
              <span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span>
            </li>
            <li>
              <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
            </li>
            <li>
              <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
                fontSize: "1.7rem",
                color: "#fff",
                cursor: "pointer",
                borderRadius: "10px",
                width: "40px",
                height: "40px",
                textAlign: "center",
                padding: "2px 8px 2px 8px",
                background: "#7d7d7d",
              }} class="ri-play-line"></i></Link>
            </li>
            <Link to={"/home"}  style={{textDecoration:"none",color:"#7d7d7d"}} >                <i  style={{fontSize:"1.9rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
</Link>          </ul>
        </div>
      </div>
    
    
    </>
  )
}

export default Services