import React,{useEffect,useState} from 'react'
import LinksHome from '../components/links-home/LinksHome'
import ChatHome from '../components/chat-home/ChatHome'
import Header from '../components/Header/Header'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Cookiesservices from '../Services/Cookiesservices'
import axios from 'axios'
import User_icon from "../assets/images/user-icon.png"
//import ReactStars from 'react-stars'
import Usericon from "../assets/images/user-icon.png"
//import data from "../assets/images/schedule.png"
const Details= () => {
    const user_id=localStorage.getItem('user_id')
const token=Cookiesservices.get('jwt')
const[profilehome,setprofilehome]=useState([])
  useEffect(()=>{
    const fetchdatauser=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/users/${user_id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setprofilehome(respose.data.data)
    }
    fetchdatauser()
  },[user_id, token]
  )
  const usser_id=localStorage.getItem("Number_id")
  const[reserve,setreserve]=useState([])
  const [items,setitems]=useState()
  const {id}=useParams();
  localStorage.setItem("id_id_id",id)
  const [user , setuser]=useState([])

  useEffect(()=>{
    const fetchdetails=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/doctors/prescription/${id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setreserve(respose.data.data.trainer)
        setuser(respose.data.data.trainer.user)


    }
    fetchdetails()

  },[id,token])
  const [dayy , setday]=useState()
  const d = new Date(reserve.date);
const time=d.toLocaleTimeString('en-us',{hours:false})
  let day=d.getDay();
//   const formattime=time.toLocaleTimeString('en-us',{hours:false})
  const timee=(time.includes("AM")?"صباحا":"مساء")
  
console.log(timee)

  useEffect(()=>{
     console.log(day)
    setday(day===0?"يوم الاحد":day===1?"يوم الاثنين":day===2?"يوم الثلاثاء":day===3?" يوم الاربعاء":day===4?"يوم الخميس":day===5?"يوم الجمعه":day===6?"السبت":"")
    localStorage.setItem("dayy",dayy)

  },[day, dayy])  
  const data=localStorage.getItem("dayy");
const navigate=useNavigate();
  return (
    <>
    <Header />
    < section style={{ width: "100%", display: 'flex', justifyContent: "space-between" }}>
      <ChatHome />
      <section className='center' style={{ background:"#fff",width: "59%", display: "flex", justifyContent: "space-between", gap: "19px" }}>
<div style={{height:"100%",width:"100%"}}>
{
    
        <div className="participants"  style={{    width: "100%"}}>
    <div className="right" style={{display: "flex",
    flexDirection: "row-reverse",
gap:"10px"}}>
        <div className="right">      <img src={user.profile?user.profile:Usericon} alt="" style={{width:"60px",height:"60px",borderRadius:"50%"}} />
</div>
<div className="left" >
    <div className="info-user-subs" style={{    display: "flex",
    justifyContent: "flex-end",gap:"35px"}}>
      <div className="information" style={{    display: "flex",
    flexDirection: "column"}}>
        
        <span style={{    fontSize: "18px",
        color: "#000",textAlign:"end"}}>{user.name}</span>
        <span style={{display:"flex",gap:"7px",alignItems:"center"}}>
        <div className="left" style={{    display: "flex",color:"red",
    flexDirection: "column",
    textAlign: "end"}}>
        <span>{data}</span>
<span style={{display:"flex",gap:"5px"}}><span style={{display:"flex"}}><span>{timee}</span><span style={{direction:"rtl"}}>{reserve.date}</span></span>
</span>
    </div>
            <button style={{border: "2px solid #05bc05",
    background: "transparent",
    padding: "5px 10px",
    borderRadius: "4px",
    color: "#05bc05"}}>مراجع</button></span>
        <div className="buttonsss" style={{    marginTop: "12px",
    display: "flex",
    gap: "10px",
    flexDirection: "column",
    alignItems: "end"}}>
            <button style={{border:"none",
    background: "#05bc05",
    padding: "2px 10px",
    borderRadius: "4px",
    color: "#fff",width:"100%"}}>موعد اونلاين</button>
            <button style={{    background: "#05b805",
    padding: "2px 10px",
    border: "none",
    borderRadius: "4px",
    color: "#fff",
    width: "max-content",
    display: "flex",
    direction: "rtl"}} onClick={()=>{navigate(`/memo/${id}`)}}>+تفاصيل</button>
        </div>
        
      </div>
    </div>
    </div>
    </div>
    
    
</div>
    
}
</div>

        

      </section>
      <LinksHome />

    </section>
    <div class="footer-mobile" style={{
      background: "#fff",
      border: "none",
      height: "48px",
      position: "sticky",
      zIndex: "9999",
      bottom: "0", display: "none"
    }}>
      <div class="container">
        <ul style={{
          paddingLeft: "0 !important", display: "flex",
          alignItems: "center",
          justifyContent: "space-between"
        }}>
          <li>

          <Link to={`${token ?`/user/${user_id}`:`/login`}`}>
          <img src={profilehome.profile!==null?profilehome.profile:User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" }} />
</Link>
          </li>
          <li>
          <Link to={"/products"} style={{textDecoration:"none"}}><span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span></Link>  
          </li>
          <li>
          <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
          </li>
          <li>
            <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
              fontSize: "1.7rem",
              color: "#fff",
              cursor: "pointer",
              borderRadius: "10px",
              width: "40px",
              height: "40px",
              textAlign: "center",
              padding: "2px 8px 2px 8px",
              background: "#7d7d7d",
            }} class="ri-play-line"></i></Link>
          </li>
          <li style={{
            color: "rgb(255, 255, 255)",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<Link to={"/home"} style={{textDecoration:"none"}}><li style={{
            color: "#fff",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<i  style={{fontSize:"1.7rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
</li>
</Link>          </li>
        </ul>
      </div>
    </div>

  </>  
  )
}

export default Details