import React, { useState } from 'react'
import "../styles/Winderthrow.css"
import usericon from "../assets/images/user-icon.png"
import axios from 'axios'
import Cookiesservices from '../Services/Cookiesservices'
import { toast } from 'react-toastify'

const Winderthrow = () => {
    const [amount,setamount]=useState();
    const token=Cookiesservices.get('jwt')
    const upload=async()=>{
        const response=await axios.post(`https://creen-program.com/api/wallets/recieve_money?payment=2&amount=${amount}`,{},{
                        headers:{
                                Authorization:`Bearer ${token}`
                        }
                })
                console.log(response.status)
             //   response.data.status===false&&response.status===400?toast.success("طلبك قيد التنفيذ"):toast.error("error")
    }
  return (
    <div>
          <div class="container " style={{padding:"0px"}}>
        <div className='wallet-bg'>
            <div class="d-flex align-items-center pt-5" style={{ flexDirection: "row-reverse",minHeight:"28vh"}}>
                <img src={usericon} class="rounded-circle mx-3" style={{width:"50px",height:"50px"}} alt=""/>
                <h1 class="text-light">احمد محمد جمعة</h1>
            </div>
            <div class="d-flex justify-content-between align-items-center" style={{minHeight:"100%",flexDirection: "row-reverse",textAlign: "end"}}>
                <p class="mx-3 my-4 text-light" style={{fontSize: "29px"}}>الرصيد المتوفر
                    <span class="d-block" style={{fontWeight: "bold",direction: "rtl",textAlign: "right"}}>5000 SAR</span>
                </p>
                <img src="./images/ic_launcher.png" style={{width:"120px"}} alt=""/>
            </div>
        </div>
        <div class="wrapper">
            <div class="app-wrapper">
                <content class="transactions-wrapper">
                    <div class="row row4">
                        <h3>سحب رصيد </h3>
                    </div>
                </content>
                <div class="container p-5">
                    <div class="form-group fill " id="part">
                        <input type="number" name="payment" onChange={(e)=>{setamount(e.currentTarget.value)}} class="form-control form-control-input myinput" id="amount" placeholder="SAR.0"
                            required=""/>
                    </div>
                    <button onClick={()=>{upload()}} type="submit" class="btn btn-success mt-2"><i
                            class="feather mr-2 icon-credit-card"></i>&nbsp;سحب</button>
                </div>
            </div>
        </div>
    </div>
    </div>
  )
}

export default Winderthrow