import React,{useEffect,useState} from 'react'
import LinksHome from '../components/links-home/LinksHome'
import ChatHome from '../components/chat-home/ChatHome'
import Header from '../components/Header/Header'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Cookiesservices from '../Services/Cookiesservices'
import axios from 'axios'
import User_icon from "../assets/images/user-icon.png"
import ReactStars from 'react-stars'
import Usericon from "../assets/images/user-icon.png"
import { toast } from 'react-toastify'
const Appointment = () => {
    const user_id=localStorage.getItem('user_id')
const token=Cookiesservices.get('jwt')
const[profilehome,setprofilehome]=useState([])
  useEffect(()=>{
    const fetchdatauser=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/users/${user_id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setprofilehome(respose.data.data)
    }
    fetchdatauser()
  },[user_id, token]
  )
const [type,settype]=useState()
const[data,setdata]=useState()
const{id}=useParams()
const [Data,setData]=useState([])
const navigate=useNavigate()
const [user , setuser]=useState([])
useEffect(()=>{
    const fetchdetailsdoctor=async()=>{
        const response=await axios.get(`https://creen-program.com/api/services/trainer/show/${id}`,{headers: {
    Authorization: `Bearer ${token}`,
  
  },
})
setData(response.data.data.trainer)
setuser(response.data.data.trainer.user)

//response.status===200?toast.success("added")&&navigate("/doctordetails"):toast.error("error")
        
    }
    fetchdetailsdoctor()
},[token, id])

const addreservation=async()=>{
const response=await axios.post(`https://creen-program.com/api/doctors/reservations/add?type=${type}&date=${data}&doctor=${id}`,{},{headers: {
    Authorization: `Bearer ${token}`,
  
  },
})

response.status===200?toast.success("added")&&navigate(`/doctordetails/${id}`):response.status===400? toast.error("data invalid"):<></>

}

  return (
    <>
    <Header />
    < section style={{ width: "100%", display: 'flex', justifyContent: "space-between" }}>
      <ChatHome />
      <section className='center' style={{ background:"#fff",width: "59%", display: "flex", justifyContent: "space-between", gap: "19px" }}>
<div className="appointment" style={{width:"100%",height:"100vh"}}>
<div style={{borderBottom:"1px solid #ddd"}}>
<Link style={{textDecoration:"none",color:"#000"}} /*to={`/trainer/${data.id}`}*/>
        <div className="info-user-subs" style={{    display: "flex",
    justifyContent: "flex-end",gap:"35px"}}>
      <div className="information" style={{    display: "flex",
    flexDirection: "column",alignItems:"end"}}>
        <span style={{    fontSize: "23px",
        color: "rgb(102 99 99)"}}>{Data.name}</span>
        <span style={{    fontSize: "24px",
        color: "rgb(102 99 99)"}}>{Data.joptitle}</span>
        <ReactStars
  count={5} 
value={user.num_of_rating?user.num_of_rating:0}
  size={32}
  color={'#ffd700'} />
      </div>
      <img src={user.profile!==""?user.profile:Usericon} alt="" style={{width:"60px",height:"60px",borderRadius:"50%"}} />
    </div>
    <h3 style={{textAlign:"end"}}>{Data.hospital}</h3>
        </Link>
</div>
<div className="appoint">
    <div className="one" style={{display:"flex",justifyContent:"end",gap:"5px",alignItems:"center"}}>
        <label htmlFor="">حجز موعد في العيادة</label>
        <input type="radio" name='موعد' onClick={()=>settype("ofline")} />
    </div>
    <div className="two" style={{display:"flex",justifyContent:"end",gap:"5px",alignItems:"center"}}>
        <label htmlFor="">حجز موعد اونلاين</label>
        <input type="radio" name='موعد' onClick={()=>settype("online")} />
    </div>
    <div className="three" style={{display:"flex",justifyContent:"end",gap:"5px",alignItems:"center"}}>
        <label htmlFor="">استشارة سريعه</label>
        <input type="radio"name='موعد' onClick={()=>settype("quick")} />
    </div>
    <div className="three">
       <input style={{    width: "100%",
    padding: "10px",
    borderRadius: "5px",
    outline: "1px solid green",
    border: "1px solid green"}} onClick={(e)=>setdata(e.currentTarget.value)} type="datetime-local" />
    </div>
</div>
<div className="bt" style={{    textAlign: "end",
    marginTop: "10px",
    marginRight: "5px"}}>
<button style={{    border: "none",
    background: "#08c808",
    color: "#fff",
    padding: "10px",
    borderRadius: "5px"}} onClick={addreservation}>موافق</button>

</div>
</div>

        

      </section>
      <LinksHome />

    </section>
    <div class="footer-mobile" style={{
      background: "#fff",
      border: "none",
      height: "48px",
      position: "sticky",
      zIndex: "9999",
      bottom: "0", display: "none"
    }}>
      <div class="container">
        <ul style={{
          paddingLeft: "0 !important", display: "flex",
          alignItems: "center",
          justifyContent: "space-between"
        }}>
          <li>

          <Link to={`${token ?`/user/${user_id}`:`/login`}`}>
          <img src={profilehome.profile!==null?profilehome.profile:User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" }} />
</Link>
          </li>
          <li>
          <Link to={"/products"} style={{textDecoration:"none"}}><span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span></Link>  
          </li>
          <li>
          <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
          </li>
          <li>
            <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
              fontSize: "1.7rem",
              color: "#fff",
              cursor: "pointer",
              borderRadius: "10px",
              width: "40px",
              height: "40px",
              textAlign: "center",
              padding: "2px 8px 2px 8px",
              background: "#7d7d7d",
            }} class="ri-play-line"></i></Link>
          </li>
          <li style={{
            color: "rgb(255, 255, 255)",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<i  style={{fontSize:"1.7rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
          </li>
        </ul>
      </div>
    </div>

  </>  
  )
}

export default Appointment