import React, { useEffect, useState } from 'react'
//import background from "../assets/images/background.jpg"
import usericon from "../assets/images/user-icon.png"
import ReactStars from 'react-stars'
import Header from '../components/Header/Header'
import { Link, useParams } from 'react-router-dom'
import axios from 'axios'
import Cookiesservices from '../Services/Cookiesservices'
import User_icon from "../assets/images/user-icon.png"
import ChatHome from '../components/chat-home/ChatHome'
import LinksHome from '../components/links-home/LinksHome'
import "../styles/service.css"
import { useNavigate } from 'react-router-dom'

const Service = () => {
        const user_id=localStorage.getItem('user_id')
        const{id}=useParams();
        const token=Cookiesservices.get('jwt')
        const [data , setdata]=useState([])
        localStorage.setItem("id_category",id)
useEffect(()=>{
        const service=async()=>{
                const response=await axios.get(`https://creen-program.com/api/services/show/${id}`,{
                        headers:{
                                Authorization:`Bearer ${token}`
                        }
                })
                setdata(response.data.data.trainers)
        }
        service()
},[id, token])

const navigate=useNavigate();
  return (
   <>
   <Header/>
   < section style={{ width: "100%", display: 'flex', justifyContent: "space-between" }}>
        <ChatHome />
        <section className='center' style={{ width: "59%",height:"100vh", display: "flex", justifyContent: "space-between", gap: "19px" }}>

        <div className="cards cards_service" style={{display: "grid",
        gridTemplateColumns: "repeat(2,1fr)",
        rowGap: "8px",
        columnGap: "5px",
        height: "100%",
        width: "100%"}}>
        {
data.length===0?<p style={{    position: "relative",
        left: "64%",
        color: "#000",
        top: "50%",
        width: "100%",
        height: "100%",
        fontSize: "22px",
        fontWeight: "bold"}}>لا يوجد مدربين</p>:
data.map((item , index)=>(
        <div className="card" key={index} style={{    width: "150px",
borderRadius: "10px",
height: "200px",
position: "relative",
alignItems: "center"}}>
        <img src={item.user.cover} alt="" style={{height:"60px",borderTopLeftRadius:"10px",borderTopRightRadius:"10px"}} />
        <div className="info-user" style={{display: "flex",
flexDirection: "column",
alignItems: "center",
position: "absolute",
bottom: "3px"}}>
        <img src={item.user.profile?item.user.profile:usericon} alt="" style={{width:"50px",height:"50px",borderRadius:"50%"}} />
        <span>{item.name}</span>
        <span>{item.joptitle}</span>
        <ReactStars
count={5} 
value={item.user.num_of_rating}
size={24}
color={'#ffd700'} />
        <div   style={{textDecoration:"none",width:"100%"}}><button style={{border: "1px solid green",
background: "transparent",
width: "100%",
borderRadius: "16px",marginBottom:"5px"}} onClick={()=>item.category_id===5?navigate(`/Appointment/${item.id}`):navigate(`/services/trainer/show/${item.id}`)}  >{item.category_id===5?"اختر الطبيب":"شاهد"}</button ></div>
        </div>
    </div>
))
        }
       

    </div>
          
        </section>
        <LinksHome />

      </section>
   
    <div class="footer-mobile" style={{
        background: "#fff",
        border: "none",
        height: "48px",
        position: "sticky",
        zIndex: "9999",
        bottom: "0", display: "none"
      }}>
        <div class="container">
          <ul style={{
            paddingLeft: "0 !important", display: "flex",
            alignItems: "center",
            justifyContent: "space-between"
          }}>
            <li>

            <Link to={`/user/${user_id}`}>
            <img src={User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" }} />
</Link>
            </li>
            <li>
            <Link to={"/products"}><span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span></Link>  
            </li>
            <li>
            <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
            </li>
            <li>
              <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
                fontSize: "1.7rem",
                color: "#fff",
                cursor: "pointer",
                borderRadius: "10px",
                width: "40px",
                height: "40px",
                textAlign: "center",
                padding: "2px 8px 2px 8px",
                background: "#7d7d7d",
              }} class="ri-play-line"></i></Link>
            </li>
            <li style={{
              color: "rgb(255, 255, 255)",
              cursor: "pointer",
              borderRadius: "50%",
              width: "40px",
              height: "40px",
              textAlign: "center",
              background: "rgb(125, 125, 125)"
            }}>

<Link to={"/home"}  style={{textDecoration:"none",color:"#7d7d7d"}} >                <i  style={{fontSize:"1.9rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
</Link>        
            </li>
          </ul>
        </div>
      </div>
   </>
  )
}

export default Service