import React,{useEffect,useState} from 'react'
import LinksHome from '../components/links-home/LinksHome'
import ChatHome from '../components/chat-home/ChatHome'
import Header from '../components/Header/Header'
import { Link, useNavigate } from 'react-router-dom'
import Cookiesservices from '../Services/Cookiesservices'
import axios from 'axios'
import User_icon from "../assets/images/user-icon.png"
//import ReactStars from 'react-stars'
import Usericon from "../assets/images/user-icon.png"
//import data from "../assets/images/schedule.png"
const Participants= () => {
    const user_id=localStorage.getItem('user_id')
const token=Cookiesservices.get('jwt')
const[profilehome,setprofilehome]=useState([])
  useEffect(()=>{
    const fetchdatauser=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/users/${user_id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setprofilehome(respose.data.data)
    }
    fetchdatauser()
  },[user_id, token]
  )
 
  const usser_id=localStorage.getItem("Number_id")
  const[reserve,setreserve]=useState([])
  const [user , setuser]=useState([])

  useEffect(()=>{
    const fetchdetails=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/services/showusers2/${usser_id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setreserve(respose.data.data.users)


    }
    fetchdetails()

  },[usser_id,token])
  const navigate=useNavigate();
  
  return (
    <>
    <Header />
    < section style={{ width: "100%", display: 'flex', justifyContent: "space-between" }}>
      <ChatHome />
      <section className='center' style={{ background:"#fff",width: "59%", display: "flex", justifyContent: "space-between", gap: "19px" }}>
<div style={{height:"100%",width:"100%"}}>
{
    reserve.length===0?"":reserve.map((item,index)=>(
        <div className="participants" style={{    padding:"20px",width: "100%",marginBottom:"20px",
    display: "flex",justifyContent:"space-between",
    flexDirection: "row-reverse",
    alignItems: "center"}}>
    <div className="right" style={{display: "flex",
    flexDirection: "row-reverse",
    alignItems: "center"}}>
        <div className="right">      <img src={item.user.profile?item.user.profile:Usericon} alt="" style={{width:"60px",height:"60px",borderRadius:"50%"}} />
</div>
<div className="left" >
    <div className="info-user-subs" style={{    display: "flex",
    justifyContent: "flex-end",gap:"35px"}}>
      <div className="information" style={{    display: "flex",
    flexDirection: "column",alignItems:"end"}}>
        <span style={{    fontSize: "18px",
        color: "#000"}}>{item.user.name}</span>
        <span style={{    fontSize: "18px",
        color: "#000"}}>{item.type==="online"?"موعد اونلاين":item.type==="ofline"?"موعد في العيادة":"استشارة سريعه"}</span>
        <div className="buttonsss" style={{    marginTop: "12px",
    display: "flex",
    gap: "10px"}}>
            <button style={{border: "2px solid #05bc05",
    background: "transparent",
    padding: "0px 5px",
    borderRadius: "4px",
    color: "#05bc05"}}>مراجع</button>
            <button style={{    background: "#05b805",
    padding: "0px 5px",
    border: "none",
    borderRadius: "4px",
    color: "#fff"}} onClick={()=>{navigate(`/Details/${item.id}`)}}>الاعدادات</button>
        </div>
        
      </div>
    </div>
    </div>
    </div>
    <div className="left" style={{    display: "flex",color:"red",
    flexDirection: "column",
    textAlign: "end"}}>
        <span>{"الجمعه"}</span>
<span style={{display:"flex",gap:"5px"}}><span style={{display:"flex"}}><span>مساء</span><span style={{direction:"rtl"}}>{item.date}</span></span>
</span>
    </div>
    
</div>
    ))
}
</div>

        

      </section>
      <LinksHome />

    </section>
    <div class="footer-mobile" style={{
      background: "#fff",
      border: "none",
      height: "48px",
      position: "sticky",
      zIndex: "9999",
      bottom: "0", display: "none"
    }}>
      <div class="container">
        <ul style={{
          paddingLeft: "0 !important", display: "flex",
          alignItems: "center",
          justifyContent: "space-between"
        }}>
          <li>

          <Link to={`${token ?`/user/${user_id}`:`/login`}`}>
          <img src={profilehome.profile!==null?profilehome.profile:User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" }} />
</Link>
          </li>
          <li>
          <Link to={"/products"} style={{textDecoration:"none"}}><span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span></Link>  
          </li>
          <li>
          <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
          </li>
          <li>
            <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
              fontSize: "1.7rem",
              color: "#fff",
              cursor: "pointer",
              borderRadius: "10px",
              width: "40px",
              height: "40px",
              textAlign: "center",
              padding: "2px 8px 2px 8px",
              background: "#7d7d7d",
            }} class="ri-play-line"></i></Link>
          </li>
          <li style={{
            color: "rgb(255, 255, 255)",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<Link to={"/home"} style={{textDecoration:"none"}}><li style={{
            color: "#fff",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<i  style={{fontSize:"1.7rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
</li>
</Link>          </li>
        </ul>
      </div>
    </div>

  </>  
  )
}

export default Participants