import React,{useEffect,useState} from 'react'
import LinksHome from '../components/links-home/LinksHome'
import ChatHome from '../components/chat-home/ChatHome'
import Header from '../components/Header/Header'
import { Link, useNavigate, useParams } from 'react-router-dom'
import Cookiesservices from '../Services/Cookiesservices'
import axios from 'axios'
import User_icon from "../assets/images/user-icon.png"
import ReactStars from 'react-stars'
import Usericon from "../assets/images/user-icon.png"
import { toast } from 'react-toastify'
const Live = () => {
    const user_id=localStorage.getItem('user_id')
const token=Cookiesservices.get('jwt')
const[profilehome,setprofilehome]=useState([])
  useEffect(()=>{
    const fetchdatauser=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/users/${user_id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setprofilehome(respose.data.data)
    }
    fetchdatauser()
  },[user_id, token])
  const [live,setlive]=useState([])
  useEffect(()=>{
    const fetchlive=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/video/live`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setlive(respose.data.data)
    }
    fetchlive()
  },[ token])
  
  return (
    <>
    <Header />
    < section style={{ width: "100%", display: 'flex', justifyContent: "space-between" }}>
      <ChatHome />
      <section className='center' style={{ background:"#fff",width: "59%", display: "flex", justifyContent: "space-between", gap: "19px" }}>
<div className="lives" style={{height:"100vh",display:"grid",gridTemplateColumns:"repeat(2,1fr)"}}>

    {
        live.map((item)=>(
            <div className="live" style={{height:"max-content",position:"relative"}}>
        <img src={item.image} style={{width: "100%",
    height: "350px",
    padding: "10px",
    borderRadius: "14px"}} alt="" />
    <div className="info" style={{    alignItems: "center",
    display: "flex",
    position: "absolute",
    bottom: "15px",
    textAlign: "end",
    right: "13px",
    gap:"6px"
}}>
        <div className="left" style={{    display: "flex",
    flexDirection: "column",color:"#fff",fontSize:"12px"}}>
            <span>{item.user.name}</span>
            <span>{item.title}</span>
        </div>
        <div className="right"><img style={{width:"50px",height:"50px",borderRadius:"50%"}} src={item.user.profile} alt="" /></div>
    </div>
    </div>
        ))
    }
</div>

        

      </section>
      <LinksHome />

    </section>
    <div class="footer-mobile" style={{
      background: "#fff",
      border: "none",
      height: "48px",
      position: "sticky",
      zIndex: "9999",
      bottom: "0", display: "none"
    }}>
      <div class="container">
        <ul style={{
          paddingLeft: "0 !important", display: "flex",
          alignItems: "center",
          justifyContent: "space-between"
        }}>
          <li>

          <Link to={`${token ?`/user/${user_id}`:`/login`}`}>
          <img src={profilehome.profile!==null?profilehome.profile:User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" ,borderRadius:"50%"}} />
</Link>
          </li>
          <li>
          <Link to={"/products"} style={{textDecoration:"none"}}><span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span></Link>  
          </li>
          <li>
          <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
          </li>
          <li>
            <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
              fontSize: "1.7rem",
              color: "#fff",
              cursor: "pointer",
              borderRadius: "10px",
              width: "40px",
              height: "40px",
              textAlign: "center",
              padding: "2px 8px 2px 8px",
              background: "#7d7d7d",
            }} class="ri-play-line"></i></Link>
          </li>
          <Link to={"/home"} style={{textDecoration:"none"}}><li style={{
            color: "#fff",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<i  style={{fontSize:"1.7rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
</li>
</Link>
        
        </ul>
      </div>
    </div>

  </>  
  )
}

export default Live