import React,{useEffect,useState} from 'react'
import LinksHome from '../components/links-home/LinksHome'
import ChatHome from '../components/chat-home/ChatHome'
import Header from '../components/Header/Header'
import { Link, useParams } from 'react-router-dom'
import Cookiesservices from '../Services/Cookiesservices'
import axios from 'axios'
import User_icon from "../assets/images/user-icon.png"
//import ReactStars from 'react-stars'
import Usericon from "../assets/images/user-icon.png"
//import data from "../assets/images/schedule.png"
import edir from "../assets/images/edit2.png"
import { useNavigate } from 'react-router-dom'
const Creatememo= () => {
    const user_id=localStorage.getItem('user_id')
const token=Cookiesservices.get('jwt')
const[profilehome,setprofilehome]=useState([])
const navigate=useNavigate()
  useEffect(()=>{
    const fetchdatauser=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/users/${user_id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setprofilehome(respose.data.data)
    }
    fetchdatauser()
  },[user_id, token]
  )
 const{id}=useParams();
 const [user , setuser]=useState([[]])
 const[reserve,setreserve]=useState([])
 const[doctor,setdoctor]=useState([])

  useEffect(()=>{
    const fetchdetails=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/doctors/prescription/${id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setreserve(respose.data.data.trainer)
        setuser(respose.data.data.trainer.details)
        setdoctor(respose.data.data.trainer.doctor)


    }
    fetchdetails()

  },[id,token])
  return (
    <>
    <Header />
    < section style={{ width: "100%", display: 'flex', justifyContent: "space-between" }}>
      <ChatHome />
      <section className='center' style={{ background:"#fff",width: "59%", display: "flex", justifyContent: "space-between", gap: "19px" }}>
<div style={{height:"100vh",width:"100%"}}>
<div className="creatememo" style={{    width: "100%"}}>
  <div className="headd" style={{    borderBottom:"1px solid #ddd",display: "flex",
    alignItems: "center",
    justifyContent: "end",paddingBottom:"10px"}}>
    <div className="left" style={{display: "flex",
    flexDirection: "column",
    alignItems: "end",
    gap: "5px"}}>
        <span>{doctor.name}</span>
        <button style={{border:"none",
    background: "#08d208",
    color: "#fff",
    padding: "2px 6px",
    borderRadius: "4px"}} onClick={()=>{navigate(`/Creatememonow/${id}`)}}>انشاء مذكرة</button>
    </div>
    <div className="right">
    <img src={/*data.user.profile?data.user.profile:*/Usericon} alt="" style={{width:"60px",height:"60px",borderRadius:"50%"}} />
    </div>
  </div>
    {
        user.map((item,index)=>(
            <div className="contenttt" key={index} style={{ borderBottom:"1px solid #ddd",marginTop:"10px",display: "flex",
    justifyContent: "space-between",    marginTop: "20px",
    paddingBottom: "10px",
    marginBottom: "10px"}}>
        <div className="left" style={{display:"flex",marginLeft:"5px",flexDirection:"column"}}>
            <button style={{border: "1px solid #05ce05",
    color: "#05ce05",
    background: "transparent",
    borderRadius: "4px"}}>تعديل</button>
            <img src={edir} alt=""  style={{width:"50px"}}/>
        </div>
        <div className="right" style={{display:"flex",flexDirection:"column",alignItems:"end",gap:"5px"}}>
            <span style={{display:"flex"}}><span>{item.time_ago}</span>:<span>تاريخ الزيارة</span></span>
            <span style={{display:"flex",flexDirection:"row-reverse"}}><span>الحالة</span>:<span>{item.details}</span></span>
            <span style={{display:"flex",flexDirection:"row-reverse"}}><span>الادوية</span>:<span>{item.pharmaceuticals}</span></span>
        </div>
    </div>
        ))
    }
    
</div>
</div>

        

      </section>
      <LinksHome />

    </section>
    <div class="footer-mobile" style={{
      background: "#fff",
      border: "none",
      height: "48px",
      position: "sticky",
      zIndex: "9999",
      bottom: "0", display: "none"
    }}>
      <div class="container">
        <ul style={{
          paddingLeft: "0 !important", display: "flex",
          alignItems: "center",
          justifyContent: "space-between"
        }}>
          <li>

          <Link to={`${token ?`/user/${user_id}`:`/login`}`}>
          <img src={profilehome.profile!==null?profilehome.profile:User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" }} />
</Link>
          </li>
          <li>
          <Link to={"/products"} style={{textDecoration:"none"}}><span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span></Link>  
          </li>
          <li>
          <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
          </li>
          <li>
            <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
              fontSize: "1.7rem",
              color: "#fff",
              cursor: "pointer",
              borderRadius: "10px",
              width: "40px",
              height: "40px",
              textAlign: "center",
              padding: "2px 8px 2px 8px",
              background: "#7d7d7d",
            }} class="ri-play-line"></i></Link>
          </li>
          <li style={{
            color: "rgb(255, 255, 255)",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<Link to={"/home"} style={{textDecoration:"none"}}><li style={{
            color: "#fff",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<i  style={{fontSize:"1.7rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
</li>
</Link>          </li>
        </ul>
      </div>
    </div>

  </>  
  )
}

export default Creatememo