import React,{useEffect,useState} from 'react'
import LinksHome from '../components/links-home/LinksHome'
import ChatHome from '../components/chat-home/ChatHome'
import Header from '../components/Header/Header'
import { Link, useParams } from 'react-router-dom'
import Cookiesservices from '../Services/Cookiesservices'
import axios from 'axios'
import User_icon from "../assets/images/user-icon.png"
import ReactStars from 'react-stars'
import Usericon from "../assets/images/user-icon.png"
import dataa from "../assets/images/schedule.png"
import { useNavigate } from 'react-router-dom'
const Doctordetails = () => {
    const user_id=localStorage.getItem('user_id')
const token=Cookiesservices.get('jwt')
const[profilehome,setprofilehome]=useState([])
const {id}=useParams();
  useEffect(()=>{
    const fetchdatauser=async()=>{
        const respose=await axios.get(`https://creen-program.com/api/users/${user_id}`,{
            headers:{
                Authorization:`Bearer ${token}`
            }
        })
        setprofilehome(respose.data.data)
    }
    fetchdatauser()
  },[user_id, token]
  )
  const [show , setshow]=useState(false)
  const showmodal=()=>{
    setshow(!show)
  }
  const [Data,setData]=useState([])
const navigate=useNavigate()
const [user , setuser]=useState([])
const [reservation , setreservation]=useState([]);

  useEffect(()=>{
    const fetchdetailsdoctor=async()=>{
        const response=await axios.get(`https://creen-program.com/api/services/trainer/show/${id}`,{headers: {
    Authorization: `Bearer ${token}`,
  
  },
})
setData(response.data.data.trainer)
setuser(response.data.data.trainer.user)

//response.status===200?toast.success("added")&&navigate("/doctordetails"):toast.error("error")
        
    }
    fetchdetailsdoctor()
},[token, id])

  const [dayy , setday]=useState()
  const d = new Date(reservation.date);
const time=d.toLocaleTimeString('en-us',{hours:false})
  let day=d.getDay();
//   const formattime=time.toLocaleTimeString('en-us',{hours:false})
  const timee=(time.includes("AM")?"صباحا":"مساء")


  useEffect(()=>{
     console.log(day)
    setday(day===0?"يوم الاحد":day===1?"يوم الاثنين":day===2?"يوم الثلاثاء":day===3?" يوم الاربعاء":day===4?"يوم الخميس":day===5?"يوم الجمعه":day===6?"السبت":"")
    localStorage.setItem("day",dayy)

  },[day, dayy])    
  useEffect(()=>{
    const fetchreservitiondoctor=async()=>{
        const response=await axios.get(`https://creen-program.com/api/doctors/details/${id}`,{headers: {
    Authorization: `Bearer ${token}`,
  
  },
})
setreservation(response.data.data.reservition)

//response.status===200?toast.success("added")&&navigate("/doctordetails"):toast.error("error")
        
    }
    fetchreservitiondoctor()
 
},[id,token])
 const data=localStorage.getItem("day");
//  console.log(reservation.date)
// console.log(timee,reservation.date)



  return (
    <>
    <Header />
    < section style={{ width: "100%", display: 'flex', justifyContent: "space-between" }}>
      <ChatHome />
      <section className='center' style={{ background:"#fff",width: "59%", display: "flex", justifyContent: "space-between", gap: "19px" }}>
<div className="appointment" style={{width:"100%",height:"100vh",position:"relative"}}>
<div style={{borderBottom:"1px solid #ddd"}}>
<Link style={{textDecoration:"none",color:"#000"}} /*to={`/trainer/${data.id}`}*/>
        <div className="info-user-subs" style={{    display: "flex",
    justifyContent: "flex-end",gap:"35px"}}>
      <div className="information" style={{    display: "flex",
    flexDirection: "column",alignItems:"end"}}>
        <span style={{    fontSize: "23px",
        color: "rgb(102 99 99)"}}>{Data.name}</span>
        <span style={{    fontSize: "24px",
        color: "rgb(102 99 99)"}}>{Data.joptitle}</span>
        <ReactStars
  count={5} 
 value={user.num_of_rating}
  size={32}
  color={'#ffd700'} />
      </div>
      <img src={user.profile?user.profile:Usericon} alt="" style={{width:"60px",height:"60px",borderRadius:"50%"}} />
    </div>
    <h3 style={{textAlign:"end"}}>{Data.hospital}</h3>
        </Link>
</div>
<div style={{marginTop:"10px",display:"flex",flexDirection:"row-reverse",justifyContent:"space-around"}}>
<div className="time" style={{    textAlign: "end",    background: "#02da02",
    borderRadius: "5px",
    width: "70%",
    padding: "5px"}}>
    <h4 style={{fontSize:"20px" , color:"#fff"}}><span >تم حجز موعد</span>/<span style={{color:"#000"}}>{reservation.type==="ofline"?"موعد في العيادة":reservation.type==="online"?"موعد اونلاين":"استشارة سريعة"}</span></h4>
    <h4 style={{fontSize:"20px",color:"#fff"}}>{data}</h4>
    <h4 style={{    display: "flex",
    justifyContent: "end",
    fontSize: "18px",color:"#fff"}}> <span style={{display:"flex",gap:"5px"}}><span style={{display:"flex",flexDirection:"row-reverse"}}><span>{timee}</span></span><span style={{    direction: "rtl"    }}>{reservation.date}</span><span></span></span>تاريخ</h4>
</div>
<div className="data" style={{display:"flex",flexDirection:"column",alignItems:"center"}}>
    <img src={dataa} alt="" style={{width:"50px"}}/>
    <button style={{    border: "none",
    padding: "10px",
    borderRadius: "5px",
    background: "#05cc05",
    color: "#fff"}} onClick={showmodal}>تغيير الموعد</button>
</div>
{
    show&&<div className="choose" style={{position: "absolute",
    background: "#fff",
    padding: "10px",
    borderRadius: "5px",
    width: "80%"}}>
    <h3 style={{textAlign:"center"}}>اختر الموعد المناسب</h3>
    <input type="datetime-local" style={{    width: "100%",
    padding: "10px",
    border: "1px solid green",
    outline: "1px solid green",
    marginTop: "5px",
    marginBottom: "10px"}} />
    <div className="btnss" style={{display: "flex",
    justifyContent: "center",
    paddingBottom: "5px",
    gap: "7px"}}>
        <button style={{    border: "none",
    padding: "10px",
    borderRadius: "5px",
    color: "#fff",
    background: "#06b006"}}>تغيير</button>
        <button style={{    border: "none",
    padding: "10px",
    borderRadius: "5px",
    color: "#fff",
    background: "#6c757d"}} onClick={()=>{setshow(false)}}>الغاء</button>
    </div>
</div>
}
</div>
</div>

        

      </section>
      <LinksHome />

    </section>
    <div class="footer-mobile" style={{
      background: "#fff",
      border: "none",
      height: "48px",
      position: "sticky",
      zIndex: "9999",
      bottom: "0", display: "none"
    }}>
      <div class="container">
        <ul style={{
          paddingLeft: "0 !important", display: "flex",
          alignItems: "center",
          justifyContent: "space-between"
        }}>
          <li>

          <Link to={`${token ?`/user/${user_id}`:`/login`}`}>
          <img src={profilehome.profile!==null?profilehome.profile:User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" }} />
</Link>
          </li>
          <li>
          <Link to={"/products"} style={{textDecoration:"none"}}><span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span></Link>  
          </li>
          <li>
          <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
          </li>
          <li>
            <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
              fontSize: "1.7rem",
              color: "#fff",
              cursor: "pointer",
              borderRadius: "10px",
              width: "40px",
              height: "40px",
              textAlign: "center",
              padding: "2px 8px 2px 8px",
              background: "#7d7d7d",
            }} class="ri-play-line"></i></Link>
          </li>
          <li style={{
            color: "rgb(255, 255, 255)",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<Link to={"/home"} style={{textDecoration:"none"}}><li style={{
            color: "#fff",
            cursor: "pointer",
            borderRadius: "50%",
            width: "40px",
            height: "40px",
            textAlign: "center",
            background: "rgb(125, 125, 125)"
          }}>

<i  style={{fontSize:"1.7rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
</li>
</Link>          </li>
        </ul>
      </div>
    </div>

  </>  
  )
}

export default Doctordetails